.checkout__total h3, .checkout__total span {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.25em;
  line-height: 140%;
  letter-spacing: 0.02em;
  margin: 0;
}

.checkout__product label {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.checkout__product input {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.06em;
  line-height: 140%;
  letter-spacing: 0.02em;
  border: none;
  cursor: auto;
}

.checkout-form__cash {
  position: relative;
}

.checkout-form__cash input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.checkout-form__cash label {
  padding-left: 36px;
  position: relative;
  cursor: pointer;
}
.checkout-form__cash label::before {
  content: "";
  width: 22px;
  height: 22px;
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid #000000;
}

.checkout-form__cash input:checked + label::after {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  left: 6px;
  top: 6px;
  background-color: #000000;
}

.checkout__button {
  display: block;
  border: none;
  background-color: #6e9c9f;
  padding: 1.38rem 3.13rem;
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.06em;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
  user-select: none;
}
@media (hover: hover) {
  .checkout__button:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.checkout__button.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 79.94em) {
  .checkout__button {
    padding: 1.38rem 2.13rem;
  }
}
@media (max-width: 47.94em) {
  .checkout__button {
    width: 100%;
    padding: 1.38rem 1rem;
    font-size: 0.9rem;
  }
}

.order-success {
  width: 100%;
}

.order-success__container {
  padding-top: 6rem;
  padding-bottom: 6rem;
}
@media (max-width: 79.94em) {
  .order-success__container {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}
@media (max-width: 47.94em) {
  .order-success__container {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}

.order-success__container h2 {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 4em;
  line-height: 110%;
  letter-spacing: 0.02em;
  color: black;
  margin: 0 0 6rem;
}
@media (max-width: 109.94em) {
  .order-success__container h2 {
    font-size: 3.44em;
  }
}
@media (max-width: 79.94em) {
  .order-success__container h2 {
    font-size: 3em;
  }
}
@media (max-width: 47.94em) {
  .order-success__container h2 {
    font-size: 2.5em;
  }
}
@media (max-width: 79.94em) {
  .order-success__container h2 {
    margin: 0 0 4rem;
  }
}
@media (max-width: 47.94em) {
  .order-success__container h2 {
    margin: 0 0 2rem;
  }
}

.order-success__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 47.94em) {
  .order-success__wrapper {
    flex-direction: column;
  }
}

.order-success__wrapper-ok {
  display: grid;
  padding-left: 116px;
  position: relative;
}
.order-success__wrapper-ok::before {
  content: "";
  width: 73px;
  height: 84px;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../img/svg/order-success.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
@media (max-width: 47.94em) {
  .order-success__wrapper-ok::before {
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (max-width: 47.94em) {
  .order-success__wrapper-ok {
    padding-left: 0;
    padding-top: 100px;
    margin-bottom: 2rem;
  }
}

.order-success__wrapper-ok p {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 2em;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: black;
  margin: 0 0 1rem;
}
@media (max-width: 109.94em) {
  .order-success__wrapper-ok p {
    font-size: 1.56em;
  }
}
@media (max-width: 79.94em) {
  .order-success__wrapper-ok p {
    font-size: 1.3em;
  }
}
@media (max-width: 47.94em) {
  .order-success__wrapper-ok p {
    font-size: 1.4em;
  }
}

.order-success__wrapper-ok span {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.06em;
  line-height: 140%;
  letter-spacing: 0.02em;
}

.order-success__button {
  display: block;
  border: 1px solid #6e9c9f;
  padding: 1.38rem 3.13rem;
  background-color: #ffffff;
  font-size: 1.4em;
  text-align: center;
  cursor: pointer;
  user-select: none;
}
@media (hover: hover) {
  .order-success__button:hover:not(.focus-visible) {
    border: 1px solid #6e9c9f;
    background-color: #6e9c9f;
    color: #ffffff;
  }
}
.order-success__button.focus-visible:focus {
  border: 1px solid #6e9c9f;
  background-color: #6e9c9f;
  color: #ffffff;
}
@media (max-width: 109.94em) {
  .order-success__button {
    font-size: 0.88em;
  }
}
@media (max-width: 47.94em) {
  .order-success__button {
    width: 100%;
    padding: 1rem;
  }
}

